:root {
  --font-main: Inter, sans-serif;
  --font-secondary: Lexend Deca, sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --darkdesaturatedblue: hsl(244, 38%, 16%);
  --verydarkblue: hsl(233, 47%, 7%);
  --slightlytransparentwhite1: hsla(0, 0%, 100%, 0.75);
  --slightlytransparentwhite2: hsla(0, 0%, 100%, 0.6);
  --softviolet: hsl(277, 64%, 61%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  background-color: var(--verydarkblue) !important;
  padding: 0;
  height: 100vh;
  display: flex;
  position: absolute;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: calc(100% - 3rem);
  left: 50%;
  transform: translateX(-50%);
  text-align: center !important;
  font-size: 0.9375rem !important;
}
@media (min-width: 64em) {
  body {
    padding: 0;
    height: 100vh;
    display: flex !important;
  }
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 39.25rem !important;
  margin: 0 auto;
}
@media (min-width: 64em) {
  .container {
    padding: 0 1.5rem;
    max-width: 68.75rem !important;
    margin: 0 auto;
  }
}

.s-card {
  border-radius: 0.9375rem;
  background-color: var(--darkdesaturatedblue);
}
.s-card__text {
  padding: 0rem 0.625rem 0rem 0.625rem;
}
@media (max-width: 26.66em) {
  .s-card__text {
    padding: 0rem 1.875rem 0rem 1.875rem;
  }
}
.s-card__stats {
  padding: 0;
  padding-bottom: 1.25rem;
  list-style: none;
}
.s-card__stats .list-item {
  letter-spacing: 0.0625rem;
  font-family: var(--font-main);
}
.s-card__stats .list-item__number {
  font-weight: 700;
  font-size: 1.375rem;
  color: var(--white);
}
.s-card__stats .list-item__title {
  font-weight: 400;
  text-transform: uppercase;
  font-size: 0.75rem;
  color: var(--slightlytransparentwhite2);
}
.s-card--img {
  max-width: 100%;
  height: auto;
  border-radius: 0.625rem;
  padding-bottom: 2.5rem;
}
.s-card--header {
  letter-spacing: 0.0625rem;
  font-weight: bold;
  font-size: 1.375rem;
  font-family: var(--font-main);
  color: var(--white);
}
.s-card--text {
  padding: 0.625rem 0rem 0.625rem 0rem;
  font-family: var(--font-main);
  color: var(--slightlytransparentwhite2);
}
.s-card span {
  color: var(--softviolet);
}

@media (min-width: 64em) {
  .s-card {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "content image";
  }
  .s-card__img {
    grid-area: image;
  }
  .s-card__text {
    padding: 4.375rem 4.375rem 4.375rem 3.4375rem;
  }
}
@media (min-width: 64em) and (min-width: 71.875em) {
  .s-card__text {
    padding: 4.375rem 4.375rem 2.5rem 3.4375rem;
  }
}
@media (min-width: 64em) {
  .s-card__stats {
    display: flex;
    justify-content: space-evenly;
  }
}
@media (min-width: 64em) and (min-width: 71.875em) {
  .s-card__stats .list-item__number {
    font-size: 1.5rem;
  }
  .s-card__stats .list-item__title {
    font-size: 0.8125rem;
  }
}
@media (min-width: 64em) {
  .s-card--img {
    max-width: 100%;
    height: 26.625rem;
    padding-left: 0.625rem !important;
    padding-bottom: 0;
  }
}
@media (min-width: 64em) and (min-width: 71.875em) {
  .s-card--img {
    height: 27.5rem;
  }
}
@media (min-width: 64em) {
  .s-card--header {
    font-size: 1.875rem;
    margin-bottom: 1.125rem;
  }
}
@media (min-width: 64em) and (min-width: 71.875em) {
  .s-card--header {
    font-size: 2.1875rem;
  }
}
@media (min-width: 64em) {
  .s-card--text {
    padding: 0.625rem 0.3125rem 0rem 0rem;
  }
}
@media (min-width: 64em) and (min-width: 71.875em) {
  .s-card--text {
    padding: 1.875rem 0.3125rem 0rem 0rem;
    font-size: 1.0625rem;
  }
}